@import './variables.scss';
@import './libs/normalize.scss';
@import './components/all.scss';

body {
  background: -moz-linear-gradient(90deg, rgba(0, 0, 255, 1) 0%, rgba(238, 130, 238, 1) 100%);
  background: -webkit-linear-gradient(90deg, rgba(0, 0, 255, 1) 0%, rgba(238, 130, 238, 1) 100%);
  background: -o-linear-gradient(90deg, rgba(0, 0, 255, 1) 0%, rgba(238, 130, 238, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ee82ee', endColorstr='#0000FF', GradientType=0 );
  background: -ms-linear-gradient(90deg, rgba(0, 0, 255, 1) 0%, rgba(238, 130, 238, 1) 100%);
  background: linear-gradient(90deg, rgba(0, 0, 255, 1) 0%, rgba(238, 130, 238, 1) 100%);
}

.wrapper {
  margin: 50px 10px 0px 10px;
  .container {
    margin: 0 auto;
    max-width: $container-width;
    padding: 50px 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-items: center;
    h1 {
      text-align: center;
      font-size: 32px;
      color: $white;
      padding: 0px 0px 30px 0px;
    }
    min-height: 400px;
    @media (min-width: 768px) {
      margin: 50px auto;
    }

    background: -moz-linear-gradient(90deg, rgba(12, 8, 66, 1) 100%, rgba(229, 43, 43, 1) 100%);
    background: -webkit-linear-gradient(90deg, rgba(12, 8, 66, 1) 100%, rgba(229, 43, 43, 1) 100%);
    background: -o-linear-gradient(90deg, rgba(12, 8, 66, 1) 100%, rgba(229, 43, 43, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#E52B2B', endColorstr='#0C0842', GradientType=0 );
    background: -ms-linear-gradient(90deg, rgba(12, 8, 66, 1) 100%, rgba(229, 43, 43, 1) 100%);
    background: linear-gradient(90deg, rgba(12, 8, 66, 1) 100%, rgba(229, 43, 43, 1) 100%);
    border-radius: 10px;
    color: $white;
  }
}
