@import '../variables';

.button {
  display: inline-block;
  border-radius: 5px;
  padding: 11px;
  font-size: 18px;
  color: $white;
  cursor: pointer;
  /* global 94%+ browsers support */
  background: linear-gradient(90deg, rgba(31, 17, 206, 1) 0%, rgba(229, 43, 43, 1) 100%);
  &:hover {
    /* global 94%+ browsers support */
    background: linear-gradient(90deg, rgba(229, 43, 43, 1) 0%, rgba(31, 17, 206, 1) 100%);
  }
}
