.icons {
  margin: 20px 0px 15px 0px;
  svg {
    font-size: 30px;
    cursor: pointer;
    transition: all 0.5s ease 0s;
    margin: 0px 10px;
    &:hover {
      color: rgb(64, 12, 185);
      transform: scale(1.2);
    }
  }
}

.disabled {
  opacity: 0.2;
  pointer-events: none;
}

.active {
  opacity: 1;
  pointer-events: auto;
}
