.input-block {
  display: flex;
  justify-content: center;
  width: 100%;
  .input-form {
    display: flex;
	 width: 100%;
    max-height: 50px;
    @media (max-width: 768px) {
    }
    .input {
		width: 100%;
      border-radius: 5px;
      padding: 10px;
      font-size: 18px;
      background-color: rgb(58, 54, 54);
      border: 2px solid #0000ff;
      color: $white;
    }
    ::-webkit-input-placeholder {
      color: $white;
    }
    :-moz-placeholder {
      color: $white;
    }
    ::-moz-placeholder {
      color: $white;
    }
    :-ms-input-placeholder {
      color: $white;
    }
    .button {
      display: inline-flex;
      min-width: 100px;
    }
  }
}
