.todo-block {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 80%;
  .todo {
    width: 100%;
    padding: 15px;
    margin: 5px 0px;
    border-radius: 5px;
    cursor: pointer;
    transition: 2s;
    font-size: 20px;
    /* global 94%+ browsers support */
    background: linear-gradient(90deg, rgba(31, 17, 206, 1) 0%, rgba(229, 43, 43, 1) 100%);
    &:hover {
      /* global 94%+ browsers support */
      background: linear-gradient(90deg, rgba(229, 43, 43, 1) 0%, rgba(31, 17, 206, 1) 100%);
    }
  }
  .icons-todo {
    position: absolute;
    top: 22px;
    right: 10px;
    display: inline-flex;
    font-size: 25px;
    svg {
      cursor: pointer;
      transition: all 0.5s ease 0s;
      margin: 0px 10px;
      &:hover {
        color: rgb(64, 12, 185);
        transform: scale(1.2);
      }
    }
  }
}

.completedTodo {
  opacity: 0.2;
}
